    font-size: 16px;
}

body {
    width: 80%;
    overflow-x: hidden;
    margin: auto;
    font-family: 'Noto Sans', 'Noto Sans JP', sans-serif;
}

p {
    margin: 0;
}

p + p {
    margin-top: 1rem;
}

.bodyWrapper p + p {
    margin-top: 0.5rem;
}

.titleShadow, .footShadow {
    height: 10px;
    width: 100vw;
    box-shadow: inset 0 10px 10px -10px #00000050;
    margin: 30px calc(50% - 50vw);
}

.titleShadow {
    box-shadow: inset 0 10px 10px -10px #00000050;
}

.footShadow {
    box-shadow: inset 0 -10px 10px -10px #00000050;
    margin-bottom: 0;
}

.title {
    text-align: center;
    display: block;
    width: fit-content;
    font-size: 2.5rem;
    font-weight: bold;
    margin: 20px auto;
}

.title a {
    color: var(--dark-text);
    text-decoration: none;
}

.about {
    margin-bottom: 30px;
}

.about .accordion {
    border-radius: 0.5rem;

    background-color: var(--pale-aqua);
    margin: 10px 0;
}

.about summary {
    cursor: pointer;
    border-radius: 0.5rem;
    font-weight: bold;
    background-color: white;
    display: block;
    user-select: none;
    border: 2px solid var(--common-aqua);
}

.about .accordion[open] summary {
    border-radius: 0.5rem 0.5rem 0 0;
}

.about .accordion[open] .accordionIcon {
    transform: rotate(180deg);
}

.about .accordionInner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 0.5rem 1rem;
}

.about .accordionMain {
    padding: 1rem;
}

.version + .version {
    border-top: 1px solid var(--common-aqua);
    margin-top: 25px;
    padding-top: 10px;
}

.historyVersion {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 1rem;
}

.historyDate {
    font-size: 0.8rem;
}

.filter {
    background-color: var(--pale-aqua);
    border-radius: 1rem;
    margin: 20px 0;
    padding: 15px;
}

.dataSource {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 15px;
}

.dataSource span {
    margin-right: 5px;
}

.dataSource select {
    width: 100%;
    max-width: fit-content;
    background-color: var(--dark-text);
    color: white;
    height: 2rem;
    border-radius: 0.5rem;
    border: none;
    text-overflow: ellipsis;
}

.search {
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
    border-top: 1px solid #aaaaaa;
    padding-top: 20px;
}

.search * {
    border: solid var(--dark-text);
    border-width: 1px 1px 1px 0;
    height: 2rem;
    margin: 0;
    box-sizing: border-box;
}

.search *:first-child {
    border-left-width: 1px;
    border-radius: 0.5rem 0 0 0.5rem;
}

.search *:last-child {
    border-right-width: 1px;
    border-radius: 0 0.5rem 0.5rem 0;
}

.textInput {
    width: 100%;
    border-radius: 0.5rem;
    padding: 0 0.5rem;
}

.ruleInput {
    background-color: var(--dark-text);
    color: white
}

.rangeWrapper {
    display: flex;
    flex-wrap: wrap;
    padding-top: 3px;
}

.checkLabel {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    margin: 3px;
    height: 2rem;
    border: 1px solid #888888;
    background-color: white;
}

.checkLabel:hover {
    background-color: var(--pale-aqua);
}

.checkLabel:has(input:checked) {
    border-color: transparent;
    background-color: var(--dark-text);
    color: white;
}

.checkLabel input {
    display: none;
}

.checkLabel span {
    user-select: none;
    margin: 0 0.5rem;
}

.topBar {
    background-color: var(--pale-yellow);
    padding: 15px;
    border-radius: 1rem;
    margin: 30px 0;
    display: flex;
    align-items: center;
    justify-content: end;
}

.topBar > * {
    margin: 0 10px;
}

.topBar p {
    margin-right: auto;
}

.sortSelect {
    background-color: var(--dark-text);
    color: white;
    height: 2rem;
    border-radius: 0.5rem;
    border: none;
}

.allOpen {
    border-color: var(--common-aqua);
    font-size: 0.8rem;
}

.allOpen:has(input:checked) {
    background-color: var(--common-aqua);
    border-color: transparent;
}

.allOpen:hover {
    background-color: var(--pale-aqua);
}

button {
    font-size: 0.8rem;
    display: block;
    height: 2rem;
    padding: 0 0.5rem;
    border-radius: 0.5rem;
    border: none;
    background-color: var(--action-yellow);
    color: white;
    cursor: pointer;
}

.shadowCard {
    box-shadow: 0 0 10px #00000040;
}

.contentWrapper {
    margin: 30px 0;
}

.entry {
    padding: 15px;
    margin: 20px 0;
    border-radius: 1rem;
}

.langName {
    font-size: 1.3rem;
    font-weight: bold;
    margin: 10px 0 !important;
}

.entryProps {
    margin: 10px 0;
    width: 100%;
    word-break: break-all;
}

.entryRow {
    width: 100%;
    justify-content: start;
    align-items: center;
    display: flex;
    flex-direction: row;
    border-top: 2px solid #dddddd;
    padding: 3px 0;
}

.entryRow:last-child {
    border-bottom: 2px solid #dddddd;
}

.entryRow:nth-child(even) {
    background-color: var(--pale-aqua);
}

.entryRow:nth-child(odd) {
    background-color: white;
}

.entryRow * {
    box-sizing: border-box;
}

.label {
    margin-left: 0.5rem;
    text-align: left;
    white-space: nowrap;
    color: var(--dark-text);
    font-weight: bold;
    width: 15rem;
    flex-grow: 1;
}

.data {
    text-align: left;
    width: 100%;
}

.valueTag {
    display: inline-block;
    height: fit-content;
    width: fit-content;

    padding: 2px 4px;
    margin: 0 5px 3px 0;
    border-radius: 5px;
    font-size: 0.7rem;
    border: 1px solid black;
}

summary::-webkit-details-marker {
    /* Safari */
    display: none;
}

.moreInfo summary {
    user-select: none;
    display: block;
    width: fit-content;
    height: fit-content;
    padding: 4px 6px;
    border-radius: 5px;
    cursor: pointer;
    
    font-size: 0.8rem;
    border: 1px solid var(--common-aqua);
}

.moreInfo summary:hover {
    background-color: var(--pale-aqua);
}

.moreInfo[open] summary {
    background-color: var(--common-aqua);
    color: white;
    font-size: 0;
}

.moreInfo[open] summary:before {
    content: "";
    font-size: 0.8rem;
}

.bottomBar {
    background-color: var(--pale-yellow);
    padding: 10px;
    border-radius: 1rem;
}

.upButton {
    margin: 15px auto;
}

footer {
    margin: 20px 0;
    text-align: center;
}

@media screen and (max-width:600px) {
    .entryRow {
        flex-direction: column;
    }

    .entryRow > * {
        width: 100%;
        margin: 5px 0;
        padding: 0 0.5rem;
    }

    .topBar {
        flex-direction: column;
        justify-content: start;
        align-items: start;
    }

    .topBar * {
        margin: 5px 0;
    }
}`, "",{"version":3,"sources":["webpack://./src/components/App.css"],"names":[],"mappings":"AAIA;IACI,eAAe;AACnB;;AAEA;IACI,UAAU;IACV,kBAAkB;IAClB,YAAY;IACZ,oDAAoD;AACxD;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,6CAA6C;IAC7C,6BAA6B;AACjC;;AAEA;IACI,6CAA6C;AACjD;;AAEA;IACI,8CAA8C;IAC9C,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,uBAAuB;IACvB,qBAAqB;AACzB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;;IAErB,kCAAkC;IAClC,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,qBAAqB;IACrB,iBAAiB;IACjB,uBAAuB;IACvB,cAAc;IACd,iBAAiB;IACjB,oCAAoC;AACxC;;AAEA;IACI,gCAAgC;AACpC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,mBAAmB;;IAEnB,oBAAoB;AACxB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,wCAAwC;IACxC,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,kCAAkC;IAClC,mBAAmB;IACnB,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,kCAAkC;IAClC,YAAY;IACZ,YAAY;IACZ,qBAAqB;IACrB,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,6BAA6B;IAC7B,iBAAiB;AACrB;;AAEA;IACI,8BAA8B;IAC9B,2BAA2B;IAC3B,YAAY;IACZ,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;IACtB,gCAAgC;AACpC;;AAEA;IACI,uBAAuB;IACvB,gCAAgC;AACpC;;AAEA;IACI,WAAW;IACX,qBAAqB;IACrB,iBAAiB;AACrB;;AAEA;IACI,kCAAkC;IAClC;AACJ;;AAEA;IACI,aAAa;IACb,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,qBAAqB;IACrB,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,uBAAuB;AAC3B;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,yBAAyB;IACzB,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,oCAAoC;IACpC,aAAa;IACb,mBAAmB;IACnB,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,oBAAoB;AACxB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kCAAkC;IAClC,YAAY;IACZ,YAAY;IACZ,qBAAqB;IACrB,YAAY;AAChB;;AAEA;IACI,gCAAgC;IAChC,iBAAiB;AACrB;;AAEA;IACI