___CSS_LOADER_EXPORT___.push([module.id, `ul.react-paginate {
    margin: 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
    padding: 0 5rem;
}

ul.react-paginate li {
    width: 3rem;
    text-align: center;
}

ul.react-paginate li a {
    cursor: default;
    position: relative;
    z-index: 100;
    width: 2rem;
    height: 2rem;
    outline: none;
}

ul.react-paginate li a::before {
    cursor: default;
    content: "";
    position: absolute;
    z-index: -100;
    top: 50%;
    left: 50%;
    width: 2rem;
    height: 2rem;
    border: gray 1px solid;
    background-color: white;
    
    display: block;
    transform: translate(-50%, -50%);
    border-radius: 50%;
}

ul.react-paginate a.active {
    color: white;
    font-weight: bold;
}

ul.react-paginate a.active::before {
    background-color: var(--common-aqua);
    border: none;

}

ul.react-paginate a.previous,
ul.react-paginate a.next,
ul.react-paginate a.break,
ul.react-paginate a.page-link {
    cursor: pointer;
}

ul.react-paginate a.previous::before,
ul.react-paginate a.next::before,
ul.react-paginate a.break::before,
ul.react-paginate a.page-link::before {
    cursor: pointer;
}

ul.react-paginate a.disabled {
    cursor: default;
    color: white;
}

ul.react-paginate a.disabled::before {
    cursor: default;
    background-color: #888888;
}

@media screen and (max-width:600px) {
    ul.react-paginate {
        justify-content: space-between;
        padding: 0;
    }

    ul.react-paginate li a::before {
        width: 8vw;
        height: 8vw;
        max-width: 2rem;
        max-height: 2rem;
    }
}`, "",{"version":3,"sources":["webpack://./src/components/pagenate.css"],"names":[],"mappings":"AAEA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,kBAAkB;IAClB,aAAa;IACb,QAAQ;IACR,SAAS;IACT,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,uBAAuB;;IAEvB,cAAc;IACd,gCAAgC;IAChC,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,oCAAoC;IACpC,YAAY;;AAEhB;;AAEA;;;;IAII,eAAe;AACnB;;AAEA;;;;IAII,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,yBAAyB;AAC7B;;AAEA;IACI;QACI,8BAA8B;QAC9B,UAAU;IACd;;IAEA;QACI,UAAU;QACV,WAAW;QACX,eAAe;QACf,gBAAgB;IACpB;AACJ","sourcesContent":["@import url(./theme.css);\r\n\r\nul.react-paginate {\r\n    margin: 20px 0;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    list-style-type: none;\r\n    padding: 0 5rem;\r\n}\r\n\r\nul.react-paginate li {\r\n    width: 3rem;\r\n    text-align: center;\r\n}\r\n\r\nul.react-paginate li a {\r\n    cursor: default;\r\n    position: relative;\r\n    z-index: 100;\r\n    width: 2rem;\r\n    height: 2rem;\r\n    outline: none;\r\n}\r\n\r\nul.react-paginate li a::before {\r\n    cursor: default;\r\n    content: \"\";\r\n    position: absolute;\r\n    z-index: -100;\r\n    top: 50%;\r\n    left: 50%;\r\n    width: 2rem;\r\n    height: 2rem;\r\n    border: gray 1px solid;\r\n    background-color: white;\r\n    \r\n    display: block;\r\n    transform: translate(-50%, -50%);\r\n    border-radius: 50%;\r\n}\r\n\r\nul.react-paginate a.active {\r\n    color: white;\r\n    font-weight: bold;\r\n}\r\n\r\nul.react-paginate a.active::before {\r\n    background-color: var(--common-aqua);\r\n    border: none;\r\n\r\n}\r\n\r\nul.react-paginate a.previous,\r\nul.react-paginate a.next,\r\nul.react-paginate a.break,\r\nul.react-paginate a.page-link {\r\n    cursor: pointer;\r\n}\r\n\r\nul.react-paginate a.previous::